import React from "react";
import {Provider} from "react-redux";
import {Reset} from "styled-reset";
import styled, {createGlobalStyle} from "styled-components/macro";
import Settings from "settings";
import Dictionary from "dictionary";
import ViewDictionary from "view_dictionary";
import Edit from "edit";
import Game from "game";
import store from "store";
import stroke0 from "stroke0.woff";
import stroke1 from "stroke1.woff";
import stroke2 from "stroke2.woff";
import stroke3 from "stroke3.woff";
import stroke4 from "stroke4.woff";
import stroke5 from "stroke5.woff";
import stroke6 from "stroke6.woff";
import stroke7 from "stroke7.woff";

const Bezel = () => {
  return <Provider store={store}>
    <LoadFonts/>
    <Frame>
      <Reset/>
      <Settings/>
      <Dictionary/>
      <Game/>
    </Frame>
    <ViewDictionary/>
    <Edit/>
    <img src="https://dimaqq.goatcounter.com/count?p=kanji-sudoku-mobile"/>
    <img src="https://static.scarf.sh/a.png?x-pxid=45808efa-53ba-4c91-9980-be08d2a83598"/>
  </Provider>;
};

export default Bezel;

// Below is for portrait mode; figure out landscape mode.
const Frame = styled.div`
  display: flex;
  flex-direction: column;
  width: 75vmin;
  margin: auto;
`;


